import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { rankTraining } from 'dump/data';

const columns = [
  {
    title: 'Xếp hạng',
    dataIndex: 'graded',
    key: 'graded',
  },
  {
    title: 'Tên tiếng anh',
    dataIndex: 'englishName',
    key: 'englishName',
  },
  {
    title: 'Từ tín chỉ',
    dataIndex: 'fromCredits',
    key: 'fromCredits',
  },
  {
    title: 'Đến tín chỉ',
    dataIndex: 'toCredits',
    key: 'toCredits',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      rankTraining.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#/rank-training/form/edit/${record.code}`} />
          <Popconfirm 
            title="Bạn có muốn xóa?" 
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record.key)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class RankLearning extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh mục xếp hạng năm đào tạo">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/rank-training/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={rankTraining} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RankLearning />
  </Page>
);



